
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.visualTaskArea {
  position: relative;
  width: 500px;
  margin: 0 auto;
}
.visualTask {
  position: absolute;
  top: 0px;
  left: 0px;
}
.contents .content{
  display: inline-block;
}
.rightArea {
  margin-left: 20px;
  vertical-align: top;
}

.input {
  padding: 10px;
  font-size: 1.3em;
  font-family: Arial, sans-serif;
  color: #555;
  border: solid 1px #CCC;
  margin: 0 0 20px;
  border-radius: 3px;
}
.input:focus {
  border: solid 1px #EEA34A;
}
.button {
  padding: 10px;
  font-size: 1.3em;
  font-family: Arial, sans-serif;
  color: #555;
  border: solid 1px #555;
  background-color: #CCC;
}
.button:hover {
  background-color: #555;
  color: #FFF;
}

.deleteButton {
  border: 2px solid #000;
}
.task {
  font-size: 1.3em;
}

